class FractalOfLife {

  constructor(emerga) {
     this.emerga = emerga;
  }
 
  run() {
     var d3 = this.emerga.d3;
     var self = this;
    
     var body = d3.select("body");
     var svg = body.append("svg")
           .attr("width", window.innerWidth)
           .attr("height", window.innerHeight);			
     initialize();
  
     function initialize() {
        window.addEventListener('resize', resizeCanvas, false);
        resizeCanvas();
     }
              
     function resizeCanvas() {
        svg
          .attr("width", window.innerWidth)
          .attr("height", window.innerHeight);
        d3.selectAll("svg > *").remove();
        redraw(40, 4, "#444444");
     }
  
     function redraw(radius, cycles, color) {
        var 
        w = window.innerWidth,
        h = window.innerWidth,
        r = radius,
        flip = 0,
        grid = new Array();
        for (i=1, m=1, d=0; i<=cycles; i+=1, m*=2, d+=2500){
        xs = Math.sqrt(Math.pow((r*m),2)-Math.pow((r*m)/2,2));
        for (x = 0; x < w+r*m ; x += xs) {
          for (y = 0; y < h+r*m; y += r*m) {
            if (flip == 1) {
              var tCoord = {"x": x, "y": y+(r*m)/2, "r": r*m, "d": d};    					   
            }
            else {
              var tCoord = {"x": x, "y": y,         "r": r*m, "d": d};			
            }
            grid.push(tCoord);
          }
          if (flip == 1) { flip = 0; } else { flip = 1; }
        }
      }	
      d3.select("svg").selectAll("circle").data(grid).enter().append("circle")
        .attr("cx", function(d) { return d.x ; })
        .attr("cy", function(d) { return d.y ; })
        .attr("r", function(d) { return d.r ; })
        .attr("stroke", color)
        .attr("stroke-width", 1.0)
        .attr("fill-opacity", 0.0)
        .attr("opacity", 1.0)
      .transition()
        .duration(5000)
        .delay(function(d) { return d.d ; })
        .each(fade);
              
      function fade() {
        var circle = d3.select(self);
        (function repeat() {
          circle = circle.transition()
            .attr("stroke-opacity", 1)
          .transition()
            .attr("stroke-opacity", 0.33)
            .each("end", repeat);
        })();
      }
    }
  }
}
module.exports = FractalOfLife;
